'use client';

import { useEffect, useRef, useState } from 'react';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

interface ARCameraProps {
  onStreamReady?: (stream: MediaStream) => void;
}

export default function ARCamera({ onStreamReady }: ARCameraProps) {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [stream, setStream] = useState<MediaStream | null>(null);
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(false);
  const [hasPermission, setHasPermission] = useState<boolean | null>(null);

  const startCamera = async () => {
    setIsLoading(true);
    setError('');

    try {
      const mediaStream = await navigator.mediaDevices.getUserMedia({
        video: {
          facingMode: 'environment', // Use back camera for AR
          width: { ideal: 1280 },
          height: { ideal: 720 }
        }
      });

      setStream(mediaStream);
      setHasPermission(true);
      
      if (videoRef.current) {
        videoRef.current.srcObject = mediaStream;
      }

      if (onStreamReady) {
        onStreamReady(mediaStream);
      }
    } catch (err) {
      console.error('Camera access error:', err);
      setHasPermission(false);
      
      if (err instanceof Error) {
        if (err.name === 'NotAllowedError') {
          setError('Camera access denied. Please allow camera permissions and try again.');
        } else if (err.name === 'NotFoundError') {
          setError('No camera found on this device.');
        } else if (err.name === 'NotSupportedError') {
          setError('Camera not supported on this device.');
        } else {
          setError('Unable to access camera. Please check your device settings.');
        }
      } else {
        setError('An unexpected error occurred while accessing the camera.');
      }
    } finally {
      setIsLoading(false);
    }
  };

  const stopCamera = () => {
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
      setStream(null);
      setHasPermission(null);
      if (videoRef.current) {
        videoRef.current.srcObject = null;
      }
    }
  };

  useEffect(() => {
    return () => {
      // Cleanup on unmount
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [stream]);

  if (error) {
    return (
      <div className="w-full max-w-2xl mx-auto p-4">
        <Alert className="mb-4">
          <AlertDescription className="text-center">
            {error}
          </AlertDescription>
        </Alert>
        <div className="text-center">
          <Button onClick={startCamera} disabled={isLoading}>
            {isLoading ? 'Requesting Access...' : 'Try Again'}
          </Button>
        </div>
      </div>
    );
  }

  if (!hasPermission) {
    return (
      <div className="w-full max-w-2xl mx-auto p-4">
        <Card className="text-center">
          <CardHeader>
            <CardTitle>Camera Access Required</CardTitle>
            <CardDescription>
              To experience AR features, we need access to your device camera
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="mb-6">
              <div className="w-24 h-24 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                <div className="w-12 h-12 bg-gray-400 rounded-lg"></div>
              </div>
              <p className="text-gray-600 mb-4">
                The AR experience overlays digital heritage information onto your camera view, 
                creating an immersive way to explore historical sites.
              </p>
            </div>
            <Button onClick={startCamera} disabled={isLoading} size="lg">
              {isLoading ? 'Requesting Access...' : 'Enable Camera'}
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="relative w-full h-full">
      <video
        ref={videoRef}
        autoPlay
        playsInline
        muted
        className="w-full h-full object-cover rounded-lg"
      />
      
      {/* AR Overlay */}
      <div className="absolute inset-0 pointer-events-none">
        {/* Heritage Site Marker Overlay */}
        <div className="absolute top-1/3 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
          <div className="bg-black bg-opacity-70 text-white px-4 py-2 rounded-lg text-center pointer-events-auto">
            <h3 className="font-bold text-lg">Heritage Site Detected</h3>
            <p className="text-sm">Historic Heritage Museum</p>
            <div className="mt-2 text-xs text-blue-300">
              Tap to learn more
            </div>
          </div>
        </div>

        {/* Distance Indicator */}
        <div className="absolute top-4 right-4 bg-green-600 text-white px-3 py-1 rounded-full text-sm font-medium">
          50m away
        </div>

        {/* AR Instructions */}
        <div className="absolute bottom-4 left-4 right-4">
          <div className="bg-black bg-opacity-50 text-white p-3 rounded-lg text-center">
            <p className="text-sm">
              Point your camera at heritage sites to discover interactive content
            </p>
          </div>
        </div>
      </div>

      {/* Control Button */}
      <div className="absolute top-4 left-4">
        <Button 
          onClick={stopCamera}
          variant="secondary"
          size="sm"
        >
          Stop Camera
        </Button>
      </div>
    </div>
  );
}
